<script>
import Modal from "@/components/Modal";
import ButtonGroup from "./ButtonGroup.vue";
import { mapGetters } from "vuex";

export default {
  components: {
    Modal,
    ButtonGroup,
  },
  data() {
    return {
      modalKey: "check",
      isCheck: false,
    };
  },
  computed: {
    ...mapGetters(["isGlobalCheck"]),
    isVisible() {
      return !this.isGlobalCheck;
    },
  },
  methods: {
    handleClickItem(modalKey) {
      this.modalKey = modalKey;
      this.isCheck = false;
    },
    handleCheck() {
      this.isCheck = !this.isCheck;
    },
  },
