<template>
  <div>
    <div v-for="(data, index) in dataSource" :key="data.title">
      <div
        class="cursor-pointer flex justify-between items-center w-48 lg:w-60 px-5 lg:px-10 py-2.5 mb-4 group hover:bg-accordion-open"
        :class="{
          'bg-accordion-open': toggleIndex === index,
        }"
        @click="handleToggle(index)"
      >
        <p
          class="text-2xl group-hover:text-main-red"
          :class="{
            'text-main-red': toggleIndex === index,
          }"
        >
          {{ data.title }}
        </p>
        <div class="w-4 h-4 flex items-center">
          <img
            src="@/assets/toggle_+.png"
            class="w-full h-full"
            v-if="toggleIndex === index"
          />
          <p v-else>-</p>
        </div>
      </div>
      <ul
        class="overflow-hidden transition-all ease-in-out duration-300"
        style="height: 0px"
        ref="subMenu"
      >
        <li v-for="item in data.list" :key="item.name" class="pl-10 mb-5">
          <router-link
            :to="`${data.path}/${item.type}`"
            class="block hover:text-main-red"
          >
            {{ item.name }}
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    dataSource: {
      props: [],
      type: Array,
    },
  },
  data() {
    return {
      toggleIndex: null,
    };
  },
  methods: {
    handleToggle(index) {
      this.toggleIndex = this.toggleIndex === index ? null : index;
    },
  },
  watch: {
    toggleIndex: {
      immediate: true,
      handler(nextIndex) {
        if (nextIndex === null && !this.$refs.subMenu) return;
        const elements = [...this.$refs.subMenu];
        if (nextIndex === null) {
          elements.forEach((item) => {
            item.style = "height: 0px";
          });
          return;
        }
        const childrenCount = elements[nextIndex].childElementCount;
        const childrenHeight = childrenCount * 44;
        elements.forEach((item, index) => {
          if (index === nextIndex) {
            item.style = `height: ${childrenHeight}px; margin-bottom: 1rem;`;
          } else {
            item.style = "height: 0px; margin-bottom: 0;";
          }
        });
      },
    },
  },
};
</script>
