<template>
  <Modal
    name="welcome"
    :isVisible="isVisible"
    @onClose="handleClose"
    @onClickMark="handleClose"
  >
    <div class="flex items-center justify-center">
      <Welcome />
    </div>
  </Modal>
</template>
<script>
import Modal from "@/components/Modal";
import Welcome from "@/components/Welcome";
import { mapGetters } from "vuex";

export default {
  components: {
    Modal,
    Welcome,
  },
  data() {
    return {
      isVisible: false,
    };
  },
  computed: {
    ...mapGetters(["isLogin"]),
  },
  methods: {
    handleClose() {
      this.isVisible = false;
    },
  },
  watch: {
    isLogin: {
      immediate: true,
      handler(newValue, oldValue) {
        if (newValue && !oldValue) {
          this.isVisible = true;
        }
      },
    },
  },
};
</script>
