<template>
  <div
    class="fixed w-full h-full z-10 top-0"
    :class="{
      block: !isVisible,
      hidden: !isVisible,
    }"
  >
    <div
      class="w-full h-full bg-black bg-opacity-60 absolute top-0"
      @click="handleClickMark"
    ></div>
    <div class="w-full flex justify-center px-4 mt-20 lg:mt-40 relative z-10">
      <div class="w-full max-w-2xl bg-white rounded-md px-5 py-10 relative">
        <button
          v-if="hasCloseButton"
          @click="$emit('onClose')"
          class="absolute right-4 top-4 w-4 h-4 rounded-full cursor-pointer flex items-center justify-center"
        >
          x
        </button>
        <slot></slot>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    name: {
      type: String,
      default: "welcome",
    },
    isVisible: {
      type: Boolean,
      default: false,
    },
    hasCloseButton: {
      type: Boolean,
      default: true,
    },
    hasMarkClick: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      openModalMap: {},
    };
  },
  methods: {
    handleClickMark() {
      if (!this.hasMarkClick) return;
      this.$emit("onClickMark");
    },
  },
  watch: {
    isVisible: {
      immediate: true,
      handler: function (value) {
        if (!this.openModalMap[this.name] && value) {
          document.querySelector("body").style = "overflow: hidden";
        }
        if (this.openModalMap[this.name] && !value) {
          document.querySelector("body").style = "overflow: visible";
        }
        this.openModalMap[this.name] = value;
      },
    },
  },
};
</script>
