import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: () => import("../views"),
    children: [
      {
        path: "/",
        name: "Home",
        component: () => import("../views/Home"),
      },
      {
        path: "video",
        component: () => import("../views/Video"),
        redirect: { path: "/jp" },
        children: [
          {
            path: "jp",
            component: () => import("../views/Video/List.vue"),
          },
          {
            path: "jp/:type",
            component: () => import("../views/Video/List.vue"),
          },
          {
            path: "ch",
            component: () => import("../views/Video/List.vue"),
          },
          {
            path: "ch/:type",
            component: () => import("../views/Video/List.vue"),
          },
          {
            path: ":category/detail/:id",
            component: () => import("../views/Video/Detail.vue"),
          },
        ],
      },
      {
        path: "comic-book",
        component: () => import("../views/ComicBook"),
        children: [
          {
            path: "/",
            component: () => import("../views/ComicBook/List.vue"),
          },
          {
            path: ":type",
            component: () => import("../views/ComicBook/List.vue"),
          },
          {
            path: "detail/:id",
            component: () => import("../views/ComicBook/Detail.vue"),
          },
          {
            path: "content/:id",
            component: () => import("../views/ComicBook/Content.vue"),
          },
        ],
      },
      {
        path: "novel",
        component: () => import("../views/Novel"),
        children: [
          {
            path: "/",
            component: () => import("../views/Novel/List.vue"),
          },
          {
            path: ":type",
            component: () => import("../views/Novel/List.vue"),
          },
          {
            path: "detail/:id",
            component: () => import("../views/Novel/Detail.vue"),
          },
        ],
      },
      {
        path: "picture",
        component: () => import("../views/Picture"),
        children: [
          {
            path: "/",
            component: () => import("../views/Picture/List.vue"),
          },
          {
            path: ":type",
            component: () => import("../views/Picture/List.vue"),
          },
          {
            path: "detail/:id",
            component: () => import("../views/Picture/Detail.vue"),
          },
        ],
      },
      {
        path: "system",
        component: () => import("../views/System"),
        children: [
          {
            path: "/",
            component: () => import("../views/System/Rule/index.vue"),
            redirect: {
              path: "policy",
            },
            children: [
              {
                path: "policy",
                component: () => import("../views/System/Rule/Policy.vue"),
              },
              {
                path: "service",
                component: () => import("../views/System/Rule/Service.vue"),
              },
              {
                path: "privacy",
                component: () => import("../views/System/Rule/Privacy.vue"),
              },
              {
                path: "disclaimer",
                component: () => import("../views/System/Rule/Disclaimer.vue"),
              },
            ],
          },
          {
            path: "link",
            component: () => import("../views/System/Link.vue"),
          },
          {
            path: "web-publish",
            component: () => import("../views/System/WebPublish.vue"),
          },
        ],
      },
    ],
  },
  {
    path: "/member",
    name: "Member",
    component: () => import("../views/Member"),
    redirect: {
      name: "UnAuth",
    },
    children: [
      {
        path: "user",
        component: () => import("../views/Member/User"),
        children: [
          {
            path: "/",
            name: "UnAuth",
            component: () => import("../views/Member/User/UnAuth"),
            redirect: {
              path: "sign-in",
            },
            children: [
              {
                path: "sign-in",
                component: () =>
                  import("../views/Member/User/UnAuth/SignIn.vue"),
              },
              {
                path: "sign-up",
                component: () =>
                  import("../views/Member/User/UnAuth/SignUp.vue"),
              },
            ],
          },
          {
            path: "reset-password",
            name: "ResetPassword",
            component: () => import("../views/Member/User/ResetPassword.vue"),
          },
          {
            path: "forget-password",
            name: "ForgetPassword",
            component: () => import("../views/Member/User/ForgetPassword.vue"),
          },
          {
            path: "forget-account",
            name: "ForgetAccount",
            component: () => import("../views/Member/User/ForgetAccount.vue"),
          },
          {
            path: "auth",
            name: "Auth",
            component: () => import("../views/Member/User/Auth"),
            meta: {
              isPrivate: true,
            },
            redirect: {
              name: "EditPassword",
            },
            children: [
              {
                path: "edit-password",
                name: "EditPassword",
                component: () =>
                  import("../views/Member/User/Auth/EditPassword.vue"),
              },
              {
                path: "edit-avatar",
                component: () =>
                  import("../views/Member/User/Auth/EditAvatar.vue"),
              },
            ],
          },
        ],
      },
      {
        path: "collect",
        component: () => import("../views/Member/Collect"),
        meta: {
          isPrivate: true,
        },
      },
      {
        path: "history",
        component: () => import("../views/Member/History"),
        meta: {
          isPrivate: true,
        },
      },
      {
        path: "announcement",
        component: () => import("../views/Member/Announcement"),
        children: [
          {
            path: "/",
            component: () => import("../views/Member/Announcement/List.vue"),
          },
          {
            path: "detail/:id",
            component: () => import("../views/Member/Announcement/Detail.vue"),
          },
        ],
      },
    ],
  },
  {
    path: "*",
    redirect: {
      name: "Home",
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
});

export default router;
