<template>
  <footer
    class="flex lg:hidden px-4 py-2.5 flex-wrap border-t-1 border-solid border-border-color text-link"
    :class="{
      'pb-10': hasFixedPagination,
    }"
  >
    <router-link
      v-for="{ path, name } in links"
      :key="name"
      :to="path"
      class="mr-4 mb-2"
    >
      {{ name }}
    </router-link>
  </footer>
</template>

<script>
export default {
  data() {
    return {
      links: [
        { path: "/system/link", name: "友情連結" },
        { path: "/system/policy", name: "政策說明" },
        { path: "/system/service", name: "服務條款" },
        { path: "/system/privacy", name: "隱私權政策" },
        { path: "/system/disclaimer", name: "免責聲明" },
        { path: "/system", name: "聯絡我們" },
      ],
    };
  },
  computed: {
    hasFixedPagination() {
      const hasFixedPaginationList = [
        "/picture/detail/",
        "/novel/detail/",
        "/comic-book/content/",
      ];
      const fullPath = this.$route.fullPath;
      return !!hasFixedPaginationList.find((item) => fullPath.includes(item));
    },
  },
};
</script>
