<template>
  <div class="flex items-center justify-center">
    <!-- TODO add checked style -->
    <input
      class="hidden"
      type="checkbox"
      :id="checkboxFor"
      @click="handleChange"
      :checked="value"
    />
    <label
      :for="checkboxFor"
      :class="{
        'bg-white border-1 border-solid border-gray-400': !value,
      }"
      class="inline-block w-6 h-6 rounded-md mr-2.5 cursor-pointer"
    />
  </div>
</template>
<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    checkboxFor: {
      type: String,
      default: "checkbox",
    },
  },
  methods: {
    handleChange() {
      this.$emit("onChange", !this.value);
    },
  },
};
</script>
<style lang="postcss" scoped>
input[type="checkbox"]:checked + label::before {
  @apply text-white bg-main-red w-full h-full rounded-md flex items-center justify-center;
  content: "\2713";
}
</style>
