<script>
import SearchInput from "@/components/SearchInput";
import Dropdown from "@/components/Dropdown";
import MobileMenu from "@/components/MobileMenu";
import Welcome from "@/components/Welcome";
import { mapActions, mapGetters } from "vuex";
import Play from "@/assets/header_play.png";
import PlayActive from "@/assets/header_play.png";
import Link from "@/assets/header_link.png";
import LinkActive from "@/assets/header_link_active.png";
import Notice from "@/assets/header_notice.png";
import NoticeActive from "@/assets/header_notice_active.png";
import Member from "@/assets/header_member.png";
import MemberActive from "@/assets/header_member_active.png";

export default {
  components: {
    Dropdown,
    SearchInput,
    MobileMenu,
    Welcome,
  },
  data() {
    return {
      isOpen: false,
      Member,
      MemberActive,
      rightLinks: [
        {
          icon: Play,
          activeIcon: PlayActive,
          // get random path
          path: "/video/ch/detail/0",
          name: "隨機播放",
        },
        {
          icon: Link,
          activeIcon: LinkActive,
          path: "/system/web-publish",
          name: "網域地址發布",
        },
        {
          icon: Notice,
          activeIcon: NoticeActive,
          path: "/member/announcement",
          name: "系統公告",
        },
      ],
    };
  },
  computed: {
    ...mapGetters(["isLogin"]),
  },
  methods: {
    ...mapActions(["logout"]),
    handleLogout() {
      this.logout();
      this.$router.push("/");
    },
    handleSearch(data) {
      console.log(data);
    },
    handleToggleSearch() {
      this.isOpen = !this.isOpen;
    },
    handleRedirect(path) {
      const width = window.innerWidth;
      if (width <= 768) return;
      this.$router.push(path);
    },
  },
};
</script>
