      <div class="w-6 h-0.5 bg-gray-600"></div>
      <div class="w-6 h-0.5 bg-gray-600"></div>
    </button>
    <div
      ref="mobileMenu"
      class="absolute top-10 bg-white z-10 -left-5 rounded-md border-1 border-solid border-gray-200"
      :class="{ hidden: !isOpen }"
    >
      <LinkAccordion :dataSource="links" />
    </div>
  </div>
</template>
<script>
import LinkAccordion from "@/components/LinkAccordion";
export default {
  components: {
    LinkAccordion,
