            },
          ],
        },
      ],
    };
  },
  methods: {
    handleToggle() {
      this.isOpen = !this.isOpen;
    },
    handleClickBody(event) {
      const { mobileButton, mobileMenu } = this.$refs;
      if (!mobileButton || !mobileMenu) return;
      const isClickButton = mobileButton.contains(event.target);
      const isClickMenu = mobileMenu.contains(event.target);
      if (!isClickButton && !isClickMenu && this.isOpen) {
        this.isOpen = false;
      }
    },
  },
  mounted() {
    document
      .querySelector("body")
      .addEventListener("click", this.handleClickBody);
  },
  watch: {
    $route() {
      this.isOpen = false;
    },
  },
};
</script>
